/* InvoiceForm.module.css */

.body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.invoice {
  width: 80%;
  margin: 20px auto;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  color: #333;
}

.header {
  background-color: #007bff;
  color: #fff;
  padding: 20px;
  text-align: center;
  margin-bottom: 43px;
  border-radius: 10px;
}

.header_h1 {
  margin: 0;
  font-size: 24px;
}

.invoice_section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.recipient_details,
.product_data {
  width: 100%;
  max-width: 600px;
  margin-bottom: 20px;
}

.form_group {
  margin-bottom: 15px;
}

.form_group_label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form_group_input_text,
.form_group_input_tel,
.form_group_textarea,
.product_row_input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  box-sizing: border-box;
}
.form_group_input_checkbox{

  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  box-sizing: border-box;
}
.product_row {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
}

.product_row_input {
  flex: 1;
}

.remove_product_btn {
  background-color: #dc3545;
  color: #fff;
  border: none;
  border-radius: 3px;
  padding: 8px 15px;
  cursor: pointer;
  position: relative;
  top: -6px;
}

.remove_product_btn:hover {
  background-color: #c82333;
}

.btn {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  margin: 3px;
  transition: background-color 0.3s ease;
}

.btn:hover {
  background-color: #0056b3;
}

.thank_you {
  margin-top: 20px;
  text-align: center;
  font-style: italic;
  color: #555;
}

.actions {
  display: flex;
  align-items: center;
  justify-content: center;
}

.cancel_button {
  background-color: #ff5454; /* Red background color */
  color: #fff; /* White text color */
  padding: 10px;
  font-size: 16px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s;
}

.cancel_button:hover {
  background-color: #cc0000; /* Darker red on hover */
}

@media (max-width: 768px) {
  .invoice {
    width: 90%;
  }

  .form_group_input_date {
    width: 100%;
  }
}
